.clearfix{
	zoom:1;
	&:before, &:after{ content:""; display:table; }
	&:after{ clear: both; }
}

.pr0 {
	padding-right: 0;
}
0
input[type="radio"] {
	text-indent: -9999px;
	position: relative;
	width: 14px;
	height: 14px;
	&:before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		background: #ffffff;
		border-radius: 50%;
	}
	&:after {
		border-radius: 50%;
	}
}

